import React, { useState, useEffect} from 'react';

import { Container, LessonList, LessonItem } from './styles';

export default function Lessons({lessons, currentLesson, onSelectLesson}) {

  const [idLesson, setIdLesson] = useState(0);

  useEffect(() => {
    setIdLesson(currentLesson.id)
  }, [currentLesson.id])

  return (
    <Container>
      <LessonList>
        {lessons.map(lesson =>(
          <LessonItem active={idLesson === lesson.id ? true : false} onClick={() => onSelectLesson(lesson.id, lesson.video_id)} key={lesson.id}>{lesson.title}</LessonItem>
        ))}
      </LessonList>        
    </Container>    
  );
}
