import React, { useState,useEffect } from 'react';
import Lessons from '../Lessons';
import { Container, VideoTitle, Player } from './styles'; 

import Youtube from 'react-youtube';

import VideoInfo from '../VideoInfo';

import api from '../../services/api';
import ObjLessons from '../../store/lessons';

export default function PlayerVideo() {

  const [lessons, setLessons] = useState([]);
  const [currentLesson, setCurrentLesson] = useState({id: 0, video_id: 'bniUkc9G75I'});


  useEffect(()=>{
    setLessons(ObjLessons);
  }, [lessons, currentLesson])

  const handleChangeLesson = (id, video_id)=> setCurrentLesson({id, video_id });

  const opts = {
    playerVars: {
      // https://developers.google.com/youtube/player_parameters
      autoplay: 0,
    },
  };
  return (
    <Container>
      <VideoTitle>LÓGICA DE PROGRAMAÇÃO</VideoTitle>
      <Player>
        <Youtube
          videoId={currentLesson.video_id}
          opts={opts}
        />
        <Lessons lessons={lessons} currentLesson={currentLesson} onSelectLesson={handleChangeLesson}></Lessons>
      </Player>
      <VideoInfo></VideoInfo>
    </Container>
  );
}
